.people {
  justify-content: center;
  gap: 30px;
  margin: 30px;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
}

@media (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, max-content);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, max-content);
  }
}

@media (width >= 1500px) {
  .people {
    grid-template-columns: repeat(4, max-content);
  }
}

.people__person {
  text-align: center;
  border-radius: 10px;
  width: 200px;
  padding: 10px;
  font-size: 18px;
  font-weight: 400;
  position: relative;
}

.people__person:hover {
  z-index: 1;
  background-color: #eee;
  transform: scale(1.35);
}

.people__person:hover .people__img {
  border-radius: 60px;
}

.people__person:hover .people__sport, .people__person:hover .people__pay {
  opacity: 1;
}

.people__photo {
  width: 200px;
  height: 200px;
  position: relative;
}

.people__img {
  object-fit: cover;
  border-radius: 50%;
  height: 100%;
  transition: all 1s ease-in-out;
}

.people__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  bottom: 84px;
  right: 10px;
}

.people__name {
  text-align: center;
  color: #2f4f4f;
  margin-top: 8px;
  font-size: 20px;
  font-weight: 700;
  transition: all 1s;
  display: block;
}

.people__extra {
  margin-top: 9px;
}

.people__sport, .people__pay {
  opacity: 0;
  color: #000;
  font-size: 18px;
  font-weight: 400;
}
